import React, { useEffect, useState } from "react";

import Border from "../../assets/common/border.svg";

import ContentContainer from "./contentContainer";
import Babylon from "../babylonjs/babylon";

const Banner = ({
  heightClass = "min-h-91",
  title,
  className,
  borderBottom,
  borderTop,
  babylon,
}: {
  heightClass?: string;
  title: string;
  className?: string;
  borderBottom?: boolean;
  borderTop?: boolean;
  babylon?: boolean;
}) => {
  const [textTranslate, setTextTranslate] = useState<
    "translate-y-full" | "translate-y-0"
  >("translate-y-full");
  useEffect(() => {
    setTextTranslate("translate-y-0");
  }, []);

  return (
    <div
      className={`w-full flex items-center bg-main-100 relative overflow-hidden px-6 ${heightClass} ${className}`}
    >
      {borderTop ? (
        <Border className="absolute top-0 left-0 right-0 w-full" />
      ) : null}
      <ContentContainer
        className={`h-full flex flex-col lg:flex-row py-12 lg:py-0 items-center text-secondary-100 flex-grow`}
      >
        <div className="flex flex-col overflow-hidden items-left h-full justify-center text-secondary-100 h-full w-full lg:w-1/2 bg-main-100">
          <h1 className={`overflow-hidden mb-8 w-full`}>
            <span
              className={`banner-title inline-block text-4xl w-full text-center transition-all duration-1000 font-light ${textTranslate}`}
            >
              {title}
            </span>
          </h1>
        </div>
        {babylon ? (
          <div className="w-[85%] lg:w-1/2 lg:h-full hidden md:flex">
            <Babylon />
          </div>
        ) : null}
      </ContentContainer>
      {borderBottom ? (
        <Border className="absolute bottom-0 left-0 right-0 w-full" />
      ) : null}
    </div>
  );
};

export default Banner;
