import React from "react";

import { HeadFC, HeadProps, PageProps } from "gatsby";
import { graphql } from "gatsby";

import { IBlogPageData, IBlogPageContext } from "../types/blog.type";

import Banner from "../components/common/banner";
import ContentContainer from "../components/common/contentContainer";
import PostPreview from "../components/posts/postPreview";
import Pagination from "../components/common/pagination";
import { HeadSeo } from "../components/common/metadata";

const Blog = ({
  data,
  pageContext,
}: PageProps<IBlogPageData, IBlogPageContext>) => {
  const posts = data.allMarkdownRemark.nodes;
  const { currentPage, pagesQuantity, markupData, lang } = pageContext;

  return (
    <>
      <Banner heightClass="h-136" title={markupData.bannerTitle} borderBottom />
      <ContentContainer className="flex-col items-center p-6 md:p-12 bg-secondary-100">
        {posts
          ? posts.map((post) => (
              <PostPreview
                key={post.id}
                post={post}
                markupData={markupData}
                fontColor="main"
                className="py-14 border-b-4 border-dashed border-secondary-100"
                gradientBorder
                lang={lang}
              />
            ))
          : null}
        <Pagination
          section={"blog"}
          pagesQuantity={pagesQuantity}
          currentPage={currentPage}
          nextPageTitle={pageContext.pagination.next}
        />
      </ContentContainer>
    </>
  );
};

export default Blog;

export const Head: HeadFC<IBlogPageData, IBlogPageContext> = ({
  pageContext: { markupData, currentPage, site, lang },
  location,
}: HeadProps<IBlogPageData, IBlogPageContext>) => {
  return (
    <HeadSeo
      title={`${
        markupData.head
      } | ${markupData.bannerTitle.toUpperCase()} | ${currentPage}`}
      description={`${markupData.head} | ${markupData.bannerTitle}`}
      siteUrl={site.siteUrl}
      path={location.pathname}
      lang={lang}
    />
  );
};

export const query = graphql`
  query ($skip: Int!, $limit: Int!, $lang: String) {
    allMarkdownRemark(
      filter: {
        fields: { sourceName: { eq: "posts" }, language: { eq: $lang } }
      }
      sort: { frontmatter: { date: DESC } }
      limit: $limit
      skip: $skip
    ) {
      nodes {
        id
        frontmatter {
          date(formatString: "DD MMM YYYY", locale: $lang)
          title
          text
          cover {
            childImageSharp {
              gatsbyImageData(
                formats: [AUTO, WEBP, AVIF]
                placeholder: BLURRED
                width: 600
              )
            }
          }
        }
        wordCount {
          words
        }
      }
    }
  }
`;
