import React from "react";

import { Link } from "gatsby";

import Button from "./button";

const Pagination = ({
  section,
  pagesQuantity,
  currentPage,
  nextPageTitle,
  className,
}: {
  section: "projects" | "blog";
  pagesQuantity: number;
  currentPage: number;
  nextPageTitle?: string;
  className?: string;
}) => {
  const pages = Array.from({ length: pagesQuantity }, (_, i) => i + 1);
  const nextPage = currentPage + 1;

  return (
    <div className={`my-16 flex justify-between w-full ${className}`}>
      {currentPage < pagesQuantity ? (
        <Link to={`/${section}/page/${nextPage}`} className="basis-1/2">
          <Button size="large" variant="dark" className="w-max">
            {nextPageTitle ?? "NEXT PAGE"}
          </Button>
        </Link>
      ) : null}

      <nav className="w-full flex justify-end">
        {pagesQuantity > 1
          ? pages.map((page) => (
              <Link
                key={`pagination-link-${page}`}
                to={`/${section}/page/${page}`}
              >
                <Button
                  size="large"
                  variant={page === currentPage ? "dark" : "light"}
                  border={page !== currentPage}
                  className="mx-[-1px]"
                >
                  {page}
                </Button>
              </Link>
            ))
          : null}
      </nav>
    </div>
  );
};

export default Pagination;
