import React from "react";

import { IButtonProps } from "../../types/button.type";

const Button = (props: React.PropsWithChildren<IButtonProps>) => {
  const { size, variant, border, onClick, className, children, ...attr } =
    props;

  const extraClasses = `${size === "small" ? "text-sm h-12" : "text-lg h-12"} ${
    variant === "dark"
      ? "bg-main-100 text-secondary-100 border-secondary-100 hover:bg-main-70 hover:outline-main-100"
      : "bg-secondary-100 text-main-100 border-main-100 hover:bg-secondary-30 hover:outline-secondary-100"
  } ${border ? "border border-solid" : ""}`;

  return (
    <button
      onClick={onClick}
      className={`flex min-h-10 min-w-10 m-2 items-center justify-center px-3 ${extraClasses} ${className}`}
      {...attr}
    >
      {children}
    </button>
  );
};

export default Button;
