import React, { useEffect, useRef, useState } from "react";

import Spinner from "../common/spinner";

const Babylon = () => {
  const [isBabylonReady, setIsBabylonReady] = useState<boolean>(false);
  const isInitial = useRef<boolean>(true);
  const canvasRef = useRef<HTMLCanvasElement>(null);

  useEffect(() => {
    if (
      !isInitial.current ||
      !canvasRef.current ||
      typeof window == "undefined"
    )
      return;

    isInitial.current = false;

    // WORKER instance creation
    const babylonWorker = new Worker(
      new URL("../../workers/babylon.worker.ts", import.meta.url),
      {
        type: "module",
      }
    );
    babylonWorker.onmessage = ({ data }) => {
      if (data === "ready") setIsBabylonReady(true);
    };

    // OFFSCREEN canvas
    const canvas = canvasRef.current;
    canvas.width = canvas.clientWidth;
    canvas.height = canvas.clientHeight;

    const offscreenCanvas = canvas.transferControlToOffscreen();
    babylonWorker.postMessage({ canvas: offscreenCanvas }, [offscreenCanvas]);

    // RESIZE handler
    const onResizeHandler = () => {
      babylonWorker.postMessage({
        width: canvas.clientWidth,
        height: canvas.clientHeight,
      });
    };
    window.addEventListener("resize", onResizeHandler, { passive: true });

    // WHEEL handler
    const onWheelHandler = (e: WheelEvent) => {
      e.preventDefault();
      babylonWorker.postMessage({ wheel: e.deltaY });
    };
    canvas.addEventListener("wheel", onWheelHandler);

    // ROTATE mouse handler
    const onMouseMoveHandler = (e: MouseEvent) => {
      babylonWorker.postMessage({
        x: e.movementX,
        y: e.movementY,
      });
    };
    canvas.addEventListener("mousedown", (e: MouseEvent) => {
      e.preventDefault();
      window.addEventListener("mousemove", onMouseMoveHandler, {
        passive: true,
      });
      window.addEventListener(
        "mouseup",
        () => window.removeEventListener("mousemove", onMouseMoveHandler),
        { once: true, passive: true }
      );
    });

    // ROTATE touch handler
    const nestedTouchMoveHandler = (
      e: TouchEvent,
      previousCoords: { x: number; y: number }
    ) => {
      const touch = e.touches[0];
      babylonWorker.postMessage({
        x: touch.screenX - previousCoords.x,
        y: touch.screenY - previousCoords.y,
      });

      return {
        newX: touch.screenX,
        newY: touch.screenY,
      };
    };
    let onTouchMoveHandler: (e: TouchEvent) => void;
    canvas.addEventListener(
      "touchstart",
      (e: TouchEvent) => {
        if (e.touches.length !== 1) return;

        const firstTouch = e.touches[0];

        e.preventDefault();
        const previousCoords = {
          x: firstTouch.screenX,
          y: firstTouch.screenY,
        };

        onTouchMoveHandler = (e: TouchEvent) => {
          const { newX, newY } = nestedTouchMoveHandler(e, previousCoords);
          previousCoords.x = newX;
          previousCoords.y = newY;
        };

        window.addEventListener("touchmove", onTouchMoveHandler, {
          passive: true,
        });
        window.addEventListener(
          "touchend",
          () => {
            window.removeEventListener("touchmove", onTouchMoveHandler);
          },
          { passive: true }
        );
      },
      { passive: true }
    );

    // UNMOUNT handler
    return () => {
      if (!window) return;

      window.removeEventListener("resize", onResizeHandler);
      window.removeEventListener("mousemove", onMouseMoveHandler);
      window.removeEventListener("touchmove", onTouchMoveHandler);
      if (canvas) {
        canvas.removeEventListener("wheel", onWheelHandler);
      }
      babylonWorker.terminate();
    };
  }, []);

  return (
    <div className={`h-full w-full relative`}>
      <canvas
        ref={canvasRef}
        className={`bg-main-100 h-91 w-full flex items-center justify-center cursor-pointer`}
      />
      {!isBabylonReady ? (
        <Spinner className="absolute top-0 left-0 right-0 bottom-0 z-10" />
      ) : null}
    </div>
  );
};

export default Babylon;
